*{
    font-family: Arial, Helvetica, sans-serif;
    color: white;
}

body {
    padding: 60px;
    background-color:rgb(31, 29, 29);
}
body h2{
    color: rgb(14, 238, 33);
}

body h3{
    color: rgb(123, 151, 202);
}

input{
    color:grey;
    font-size: 1.2rem;
    width : 100%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom:10px ;
    margin-right:10px;
    overflow: hidden;
}

.card p{
    padding:5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}